"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import { Plus, Edit, Trash2 } from "lucide-react"
import { useToast } from "@/hooks/use-toast"
import { Textarea } from "@/components/ui/textarea"
import type { Driver } from "@/lib/types"

interface DriverManagerProps {
  drivers: Driver[]
  onDriverAdded: () => void
  onDriverUpdated: () => void
}

export function DriverManager({
  drivers,
  onDriverAdded,
  onDriverUpdated,
}: DriverManagerProps) {
  const { toast } = useToast()
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [editingDriver, setEditingDriver] = useState<Driver | null>(null)
  const [driverData, setDriverData] = useState({
    name: "",
    phone: "",
    additionalInfo: "",
  })
  const [isSaving, setIsSaving] = useState(false)
  const [isDeleting, setIsDeleting] = useState<string | null>(null)

  const handleAdd = () => {
    setEditingDriver(null)
    setDriverData({
      name: "",
      phone: "",
      additionalInfo: "",
    })
    setIsDialogOpen(true)
  }

  const handleEdit = (driver: Driver) => {
    setEditingDriver(driver)
    setDriverData({
      name: driver.name,
      phone: driver.phone || "",
      additionalInfo: driver.additionalInfo || "",
    })
    setIsDialogOpen(true)
  }

  const handleSave = async () => {
    if (!driverData.name.trim()) {
      toast({
        title: "",
        description: "   ",
        variant: "destructive",
      })
      return
    }

    setIsSaving(true)
    try {
      const url = editingDriver
        ? `/api/drivers/${editingDriver.id}`
        : "/api/drivers"
      const method = editingDriver ? "PUT" : "POST"

      const response = await fetch(url, {
        method,
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          name: driverData.name.trim(),
          phone: driverData.phone.trim() || undefined,
          additionalInfo: driverData.additionalInfo.trim() || undefined,
        }),
      })

      if (!response.ok) {
        throw new Error("Failed to save driver")
      }

      toast({
        title: "",
        description: editingDriver
          ? "  "
          : "  ",
      })

      setIsDialogOpen(false)
      setEditingDriver(null)

      // Dispatch custom event to refresh all sections
      window.dispatchEvent(new CustomEvent("refreshDropdownData"))

      if (editingDriver) {
        onDriverUpdated()
      } else {
        onDriverAdded()
      }
    } catch (error) {
      toast({
        title: "",
        description: "   ",
        variant: "destructive",
      })
    } finally {
      setIsSaving(false)
    }
  }

  const handleDelete = async (driverId: string) => {
    if (!confirm("      ?")) {
      return
    }

    setIsDeleting(driverId)
    try {
      const response = await fetch(`/api/drivers/${driverId}`, {
        method: "DELETE",
      })

      if (!response.ok) {
        throw new Error("Failed to delete driver")
      }

      toast({
        title: "",
        description: "  ",
      })

      // Dispatch custom event to refresh all sections
      window.dispatchEvent(new CustomEvent("refreshDropdownData"))

      onDriverUpdated()
    } catch (error) {
      toast({
        title: "",
        description: "   ",
        variant: "destructive",
      })
    } finally {
      setIsDeleting(null)
    }
  }

  return (
    <>
      <Button
        type="button"
        size="sm"
        variant="outline"
        onClick={handleAdd}
        className="gap-1.5 flex-shrink-0 h-10"
      >
        <Plus className="w-3.5 h-3.5" />
        
      </Button>

      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="max-w-md">
          <DialogHeader>
            <DialogTitle>
              {editingDriver ? " " : " "}
            </DialogTitle>
            <DialogDescription>
              {editingDriver
                ? "  ."
                : "  ."}
            </DialogDescription>
          </DialogHeader>

          <div className="space-y-4">
            <div>
              <Label htmlFor="driver-name">  *</Label>
              <Input
                id="driver-name"
                value={driverData.name}
                onChange={(e) =>
                  setDriverData({ ...driverData, name: e.target.value })
                }
                placeholder="   "
                autoFocus
                required
              />
            </div>

            <div>
              <Label htmlFor="driver-phone"> </Label>
              <Input
                id="driver-phone"
                type="tel"
                value={driverData.phone}
                onChange={(e) =>
                  setDriverData({ ...driverData, phone: e.target.value })
                }
                placeholder="  "
              />
            </div>

            <div>
              <Label htmlFor="driver-additional-info"> </Label>
              <Textarea
                id="driver-additional-info"
                value={driverData.additionalInfo}
                onChange={(e) =>
                  setDriverData({ ...driverData, additionalInfo: e.target.value })
                }
                placeholder="  "
                rows={3}
              />
            </div>

            {drivers.length > 0 && (
              <div>
                <Label className="mb-2 block">  </Label>
                <div className="space-y-2 max-h-40 overflow-y-auto">
                  {drivers.map((driver) => (
                    <div
                      key={driver.id}
                      className="flex items-center justify-between p-2 bg-gray-50 rounded border border-gray-200"
                    >
                      <div className="flex-1">
                        <span className="text-sm text-gray-700 font-medium">{driver.name}</span>
                        {driver.phone && (
                          <span className="text-xs text-gray-500 ml-2">({driver.phone})</span>
                        )}
                      </div>
                      <div className="flex items-center gap-1">
                        <Button
                          type="button"
                          size="sm"
                          variant="ghost"
                          onClick={() => handleEdit(driver)}
                          className="h-7 w-7 p-0"
                        >
                          <Edit className="w-3.5 h-3.5" />
                        </Button>
                        <Button
                          type="button"
                          size="sm"
                          variant="ghost"
                          onClick={() => handleDelete(driver.id)}
                          disabled={isDeleting === driver.id}
                          className="h-7 w-7 p-0 text-red-600 hover:text-red-700"
                        >
                          {isDeleting === driver.id ? (
                            <span className="text-xs">...</span>
                          ) : (
                            <Trash2 className="w-3.5 h-3.5" />
                          )}
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>

          <DialogFooter>
            <Button
              type="button"
              variant="outline"
              onClick={() => setIsDialogOpen(false)}
              disabled={isSaving}
            >
              
            </Button>
            <Button onClick={handleSave} disabled={isSaving}>
              {isSaving ? " ..." : editingDriver ? "" : ""}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </>
  )
}

